import { writable, derived } from "svelte/store";
const createNotifications = function createNotificationStore() {
    const { subscribe, set, update } = writable({
        active: false,
        message: "",
        type: "success"
    });
    return {
        subscribe,
        setMessage: (msg) => update((state) => {
            state.message = msg;
            return state;
        }),
        toggleActive: () => update((state) => {
            state.active = !state.active;
            return state;
        }),
        setType: (type) => update((state) => {
            state.type = type;
            return state;
        }),
        setNotif(msg, delay = 5, type = "success") {
            this.setMessage(msg);
            this.setType(type);
            this.toggleActive();
            setTimeout(this.toggleActive, delay * 1000);
        }
    };
};
export const notifications = createNotifications();
export const getMessage = derived([notifications], ([$notifications]) => {
    return $notifications.message;
});
export const getType = derived([notifications], ([$notifications]) => {
    return $notifications.type;
});
export const isActive = derived([notifications], ([$notifications]) => {
    return $notifications.active;
});
