<script lang="ts">

    import {usersAPI} from "../../api/usersAPI";
    import {push} from 'svelte-spa-router';
	import type { AxiosError } from "axios";
    import {user} from "../../stores/userStore";
    import {notifications as notif} from "../../stores/notificationStore";

    let username: string = '';
    let password: string = '';
    let remember: boolean = false;
    let usernameErrs: string[] = [];
    let passwordErrs: string[] = [];
    let disableBtn: boolean = false;
    
    async function submit(){
        disableBtn = true;
        usernameErrs = [];
        passwordErrs = []; 
        try {
           const res = await usersAPI.loginUser(username, password, remember); 
        
           await user.getUser();
           console.log($user);
           notif.setNotif("log in successful", 5)
           push(`/user/home`);
           
   
        } catch (error) {
            const axErr = (error as AxiosError).response;
            notif.setNotif("error logging in", 5, "danger");
            if (axErr.status == 400) {
                if (axErr.data.hasOwnProperty('username')) usernameErrs = axErr.data["username"];
                
                if (axErr.data.hasOwnProperty('password'))
                passwordErrs = axErr.data["password"];
            }
        }
        
        finally {
            disableBtn = false;
        }
    }
    function gotoRegister() {
        push("/register")
    }
</script>

<main>
    <h1>Login to t-notes</h1>

    <div class="login-container">
        <div class="form-item">
            <input type="text" placeholder="username" bind:value={username}/>
            {#each usernameErrs as err}
                <span class="error">{err}</span>
            {/each}
        </div>
        <div class="form-item">
            <input type="password" placeholder="password" bind:value={password} />
            {#each passwordErrs as err}
                <span class="error">{err}</span>
            {/each}
        </div>
        <div class="form-item check-item">
            <input type="checkbox" bind:checked={remember}>
            <span>stay signed in</span>
        </div>
        <div class="form-item button">
            <button class="btn" on:click={submit}  disabled='{disableBtn}'>Log In</button>
           
        </div>
        <div class="form-item">
            <span class="register">Don't have an account? <button on:click={gotoRegister}>register here</button></span>
        </div>
    </div>
</main>


