<script lang="ts">
    import {push} from 'svelte-spa-router';
	import type { AxiosError } from "axios";
    import {usersAPI} from "../../api/usersAPI";
    let username: string = '';
    let password: string = '';
    let password2: string = '';
    let email: string = '';

    let usernameErrs: string[]= [];
    let passwordErrs: string[]= [];
    let password2Errs: string[]= [];
    let emailErrs: string[]= [];


    async function registerUser(){
        const form = new FormData();

        usernameErrs = [];
        passwordErrs = [];
        password2Errs = [];
        emailErrs = [];

        form.append("username", username);
        form.append("email", email);
        form.append("password", password);
        form.append("password2", password2);

        try {
            const res = await usersAPI.registerUser(form);
            
        } catch (error) {
            const axErr = (error as AxiosError).response;
            if (axErr) {
                if (axErr.status == 400) {
                    const errors = axErr.data;

                    if (errors.hasOwnProperty('username'))
                        usernameErrs = errors['username'];
                    
                    if (errors.hasOwnProperty('email'))
                        emailErrs = errors['email'];
                    
                    if (errors.hasOwnProperty('password'))
                        passwordErrs = errors['password'];
                    
                    if (errors.hasOwnProperty('password2'))
                        password2Errs = errors['password2'];
                }
                

            }
        }
    }

    function gotoHome() {
        push("/")
    }

</script>


<main>
    <h1>Create an account</h1>

    <div class="form-container">
        <div class="form-item">
            <input type="text" placeholder="username" bind:value={username}/>
            {#each usernameErrs as err}
                <span class="error">{err}</span>
            {/each}
        </div>
        <div class="form-item">
            <input type="email" placeholder="email" bind:value={email}/>
            {#each emailErrs as err}
                <span class="error">{err}</span>
            {/each}
        </div>
        <div class="form-item">
            <input type="password" placeholder="password" bind:value={password} />
            {#each passwordErrs as err}
                <span class="error">{err}</span>
            {/each}
        </div>
        <div class="form-item">
            <input type="password" placeholder="repeat password" bind:value={password2} />
            {#each password2Errs as err}
                <span class="error">{err}</span>
            {/each}
        </div>
        <div class="form-item button">
            <button class="btn" on:click={registerUser}>Register</button>
        </div>
        <div class="form-item">
            <span class="login">Already have an account? <button on:click={gotoHome}>sign in here</button></span>
        </div>
    </div>
</main>

