<script lang="ts">
	import {onMount} from "svelte";
	import {
		getNoteContent,
		noteEditState,
		notes,
	} from "../../../stores/collectionStore";

	import {notesAPI} from "../../../api/notesAPI";

	export let deleteFN = undefined;
	export let collection_id: number = -1;
	// export let contentHeight = 24;
	export let closeFN = undefined;

	type EditType = "add" | "edit";

	export let type: EditType = "add";

	let textarea: HTMLTextAreaElement;
	let noteTitle: string = "";
	let noteBody: string = "";
	let date: string = new Date().toLocaleDateString("en-GB", {
		weekday: "short",
		year: "2-digit",
		month: "short",
		day: "2-digit",
		hour: "2-digit",
		minute: "2-digit",
	});
	const autoHeight = function(){
		textarea.style.height = "40px";
		if (textarea.scrollHeight)
		textarea.style.height = `${textarea.scrollHeight}px`;
	}
	const submit = async function editOrSaveNote() {
		try {
			if (type === "add") {
				const res = await notesAPI.addNote(
					collection_id,
					noteTitle,
					noteBody
				);
			} else {
				const res = await notesAPI.editNote(
					collection_id,
					$noteEditState.editId,
					noteTitle,
					noteBody
				);
			}

			await notes.getNotes(collection_id);
		} catch (error) {
			alert(error);
		} finally {
			closeFN();
		}
	};

	const deleteNote = async function() {
		try {
			await notes.deleteNote(collection_id, $noteEditState.editId);
			await notes.getNotes(collection_id);
		} catch (error) {
			alert(error);
		} finally {
			closeFN();
		}
		
	}

	const focusBody = function focusTextAreaOnClick() {
		textarea.focus();
	};

	onMount(() => {
		textarea.focus();
		textarea.style.height = `${$noteEditState.contentHeight}px`;
		if (type === "edit") {
			noteTitle = $getNoteContent.title;
			noteBody = $getNoteContent.content;
			date = new Date($getNoteContent.date).toLocaleDateString("en-GB", {
				weekday: "short",
				year: "2-digit",
				month: "short",
				day: "2-digit",
				hour: "2-digit",
				minute: "2-digit",
			});
		}
	});
</script>

<div class="parent parentFixed" on:click|stopPropagation={()=>{}}>
	<div class="title">
		<span>
			<input type="text" bind:value={noteTitle} placeholder="title" />
		</span>
	</div>
	<div class="bottomBorder" />
	<div class="body" on:click={focusBody}>
		<span>
			<textarea
				name="body"
				id="body"
				on:input={autoHeight}
				bind:value={noteBody}
				bind:this={textarea}></textarea>
		</span>
	</div>
	<div class="bottom">
		<span> {date} </span>
	</div>
	{#if deleteFN}
		<div class="btn btnDelete">
			<button on:click={deleteNote}>delete</button>
		</div>
	{/if}
	<div class="btn btnSave">
		<button on:click={submit}>save</button>
	</div>
	<div class="btn btnClose">
		<button on:click={closeFN}>close</button>
	</div>
</div>

