<script lang="ts">
	import {ToggleDark} from "./components";
	import Toast from "../toast/Toast.svelte";
	import {isActive} from "../../stores/notificationStore";
	import {user} from "../../stores/userStore";
	import {notifications as notif} from "../../stores/notificationStore";
	import {Profile, Settings, Logout, ProfileCircle} from "./svgs";
	import { push } from "svelte-spa-router";

	let menu_active = false;

	function toggle_menu_active() {
		menu_active = !menu_active;
	}

	async function signOut () {
		try {
			const res = await user.logoutUser();

			notif.setNotif(res.data.status, 3.5);
			push("/");

		} catch (error) {
			notif.setNotif("An error occured", 3.5, "danger");
		}
	}

	function gotoHome() {
		push("/")
	}
</script>

<nav>
	<button on:click={gotoHome}>T-notes</button>

	<div class="options">
		<!-- <div class="settings">
			<Settings />
		</div> -->
		<ToggleDark />
		<div class="profile" on:click={toggle_menu_active}>
			<span class="account" title="account menu">
				<ProfileCircle />
			</span>
			

			{#if menu_active}
				<div class="menu-overlay">

				</div>
				<div class="profile-menu">
					<div class="menu-item">
						<div class="option">
							<span><Profile/></span>
							{#if $user.auth}
								{$user.username}
							{:else}
								Not logged in
							{/if}
						</div>
					</div>
					{#if $user.auth}
					<div class="menu-item" title="log out">
						<div class="option" on:click={signOut}>
							<span><Logout /></span> Log out
						</div>
					</div>
					{/if}
					
				</div>
			{/if}
			
		</div>
	</div>
	

	{#if $isActive}
		<Toast />
	{/if}
	
</nav>

